html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
  background: rgb(168, 168, 168);
  background: linear-gradient(180deg, rgb(168, 168, 168) 4%, rgb(206, 81, 123) 18%, rgb(230, 25, 94) 30%, rgb(54, 54, 54) 60%);
}
body.espacio {
  padding-top: 64px;
}

h1, h2, h3, h4, p, li {
  color: #ffffff;
  text-align: center;
}

p {
  font-size: 2rem;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 2rem 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
}

li {
  list-style: none;
  margin: 0;
}

.btn {
  padding: 1rem 2rem;
  background-color: transparent;
  color: #e6195e;
  font-family: "Nunito", sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  border: 2px solid #e6195e;
  border-radius: 1rem;
  transition: all 0.3s ease-in;
  display: inline-block;
}
.btn:hover {
  background-color: #e6195e;
  color: #ffffff;
  transform: scale(1.1);
  cursor: pointer;
}

.banner {
  position: relative;
  min-height: 50rem;
  max-height: 700px;
  overflow: hidden;
}
.banner .contenido-video {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.banner video {
  position: relative;
  width: auto;
  height: 100%;
  display: block;
}
@media (min-width: 768px) {
  .banner video {
    width: 100%;
  }
}

.texto-header {
  z-index: 1000;
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  font-size: 1.4rem;
}
.texto-header span {
  color: #e6195e;
}

.navegacion {
  margin: 0;
  padding: 0 auto;
}
@media (min-width: 768px) {
  .navegacion {
    height: 4em;
  }
}

.navegacion-principal {
  margin: 1rem auto;
  padding: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal {
    margin: 0;
    display: flex;
    justify-content: space-between;
  }
  .navegacion-principal.fijo {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    background-color: #e6195e;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  justify-content: center;
  color: #ffffff;
  font-size: 1.4rem;
  font-weight: bold;
  text-transform: uppercase;
  padding: 1rem 2rem;
  transition-property: color;
  transition-duration: 0.8s;
}
.navegacion-principal a:hover {
  color: #a8a8a8;
}
.navegacion-principal a.seleccionado {
  border-bottom: 2px solid #484848;
}

.about {
  padding-top: 4.8rem;
  display: block;
  text-align: center;
  opacity: 0;
}
@media (min-width: 768px) {
  .about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.quien-soy {
  padding: 1rem;
  justify-content: center;
  border-bottom: 2px solid #e6195e;
}
@media (min-width: 768px) {
  .quien-soy {
    border-bottom: none;
  }
}
.quien-soy img {
  margin: 2rem auto;
  max-width: 200px;
  border-radius: 50%;
}
.quien-soy h2 {
  margin: 0;
}
.quien-soy a {
  padding: 1rem 2rem;
  font-size: 2rem;
  color: #ffffff;
  transition: color 0.5s ease;
}
.quien-soy a:hover {
  color: #e6195e;
  border-radius: 2rem;
}

.skills {
  margin-top: 6rem;
  text-align: center;
}
@media (min-width: 768px) {
  .skills ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .skills ul li {
    border: none;
  }
}
.skills li {
  display: block;
  margin: 2rem auto;
  font-size: 1.8rem;
  font-weight: bold;
  padding-bottom: 1rem;
  border-bottom: 1px solid #a8a8a8;
  transition-property: color, transform, background-color;
  transition-duration: 0.8s;
}
.skills li:last-of-type {
  border-bottom: none;
}
.skills li:hover {
  transform: scale(1.2);
  color: #363636;
}
.skills i {
  font-size: 2.4rem;
  margin-right: 1rem;
}

.proyectos {
  padding-top: 4.8rem;
  justify-content: center;
  text-align: center;
  opacity: 0;
}

.trabajos {
  display: block;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .trabajos {
    text-align: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .trabajos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.proyecto-contenido {
  margin: 1rem auto;
  padding: 1rem;
  min-width: 250px;
  max-width: 300px;
  background-color: #8f8f8f;
  border-radius: 1rem;
}
.proyecto-contenido p {
  font-weight: bold;
}
.proyecto-contenido img {
  padding: 1rem;
  border-radius: 1rem;
}

.descripcion-visible {
  display: none;
}

.contacto {
  opacity: 0;
  padding-top: 4.8rem;
}

.formulario {
  max-width: 500px;
  margin: 0 auto;
}
.formulario .campo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0.5rem auto;
}
.formulario .campo label {
  flex: 0 0 10rem;
  color: #ffffff;
  font-weight: bold;
}
.formulario .campo input, .formulario .campo textarea {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  background-color: #484848;
  color: #ffffff;
}
.formulario .btn {
  margin-top: 1rem;
}
.formulario .btn:hover {
  transform: scale(1);
}

.footer {
  margin: 6rem 0 4rem 0;
}

.redes {
  max-width: 300px;
  margin: 0 auto;
}
.redes nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.redes nav a {
  font-size: 3rem;
  padding: 3rem;
  color: #ffffff;
  background-color: #484848;
  border-radius: 1rem;
  position: relative;
  transition: all 1s ease;
}
.redes nav a:hover {
  background-color: #e6195e;
  top: -10px;
}

body.fijar-body {
  overflow: hidden;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.95);
  width: 100%;
  height: 100%;
}
.modal div {
  display: grid;
  place-items: center;
  overflow: hidden;
  padding: 4rem;
}
.modal div img {
  max-width: 50rem;
}
.modal div.descripcion-visible p {
  display: block;
}